import * as React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation, useNavigate } from 'react-router';

import queryString from 'query-string';

import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import { styled, alpha } from '@mui/material/styles';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import IconButton from '@mui/material/IconButton';
import AccountCircle from '@mui/icons-material/AccountCircle';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import LogoutIcon from '@mui/icons-material/Logout';
import MenuIcon from '@mui/icons-material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import useMediaQuery from '@mui/material/useMediaQuery';
import HomeIcon from '@mui/icons-material/Home';
import { useTheme } from '@mui/material/styles';
import SearchIcon from '@mui/icons-material/Search';

import { startLogout } from '../../actions/auth';
import { clearFilteredProducts, searchProducts } from '../../actions/products';


const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('lg')]: {
    marginLeft: theme.spacing('25%'),
    width: 'auto',
  },
  [theme.breakpoints.down('lg')]: {
    marginLeft: theme.spacing('20%'),
    width: 'auto',
  },
  [theme.breakpoints.between('sm', 'md')]: {
    marginLeft: theme.spacing('17.5%'),
    width: 'auto',
  },
  [theme.breakpoints.down('sm')]: {
    width: '100%',
  },
}));


const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('lg')]: {
      width: '42.5vw',
      '&:focus': {
        width: '45vw',
      },
    },
    [theme.breakpoints.between('sm', 'lg')]: {
      width: '28vw',
      '&:focus': {
        width: '28.5vw',
      },
    },
    [theme.breakpoints.down('sm')]: {
      width: '100',
    },
  }
}));


export const NavbarLogged = () => {

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const location = useLocation();

  const { q = '' } = queryString.parse(location.search);

  const { role, name } = useSelector(state => state.auth);
  const { categories } = useSelector(state => state.categories);
  
  const [searchText, setSearchText] = React.useState(q)
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [anchorMenu, setAnchorMenu] = React.useState(null);


  const theme = useTheme();
  const sm = useMediaQuery(theme.breakpoints.down('sm'));
  const md = useMediaQuery(theme.breakpoints.down('md'));
  const xl = useMediaQuery(theme.breakpoints.down('xl'));
  const lg = useMediaQuery(theme.breakpoints.down('lg'));



  React.useEffect(() => {

    
    (searchText !== '' && location.search !== '') ? dispatch( searchProducts(searchText) ) : dispatch( clearFilteredProducts() )

    
  }, [location]);


  // Search
  const handleInputChange = (e) => {
    e.preventDefault();
    setSearchText(e.target.value)
  }

  const handleSearch = (e) => {
    e.preventDefault();

    console.log(searchText);

    navigate(`?q=${searchText}`)

    dispatch( searchProducts(searchText) )


  }

  // Normal menu
  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleLogout = () => {
    handleClose();
    dispatch(startLogout());
  }

  const handleClose = () => {
    setAnchorEl(null);
  };

  
  // Responsive menu
  const handleResponsiveMenu = (event) => {
    setAnchorMenu(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setAnchorMenu(null);
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar
        sx={{
          position: 'static',
          backgroundColor: '#154585'
        }}>
        <Toolbar>

          {
            (sm)
            &&
            <>
              <IconButton
                size="large"
                edge="start"
                color="inherit"
                aria-label="menu"
                onClick={handleResponsiveMenu}
                sx={{ mr: 2 }}
              >
                <MenuIcon />
              </IconButton>

              <Menu
                id="menu-appbar"
                anchorEl={anchorMenu}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                sx={{
                  width: '90vw',
                  marginTop: '7%',
                  marginLeft: '3%',
                  '& .MuiMenuItem-root': {
                    width: '100vw'
                  }
                }}
                open={Boolean(anchorMenu)}
                onClose={handleCloseMenu}
              >
                <MenuItem onClick={handleCloseMenu}><AccountCircle />  Profile</MenuItem>
                {
                  (role === 'USER_ROLE')
                  &&
                  <MenuItem onClick={handleCloseMenu}><ShoppingCartIcon />  My Cart</MenuItem>
                }
                <MenuItem onClick={handleLogout}><LogoutIcon />  Logout</MenuItem>
              </Menu>
            </>
          }

          {
            (!sm)
            &&
            <>

              <IconButton
                color="inherit"
                edge="start"
                onClick={() => navigate('/', { replace: true })}
                sx={{
                  marginRight: '36px',
                }}
              >
                <HomeIcon />

              </IconButton>
              {
                (role !== 'USER_ROLE')
                &&
                <Typography
                  variant="p"
                  component="div"
                  onClick={() => navigate('/dashboard', { replace: true })}
                  sx={{
                    cursor: 'pointer',
                  }}>

                  Panel de control

                </Typography>
              }

            </>
          }


          <Search>

            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>

            <form onSubmit={handleSearch}>
              <StyledInputBase
                onChange={handleInputChange}
                type='text'
                name='searchText'
                autoComplete='off'
                value={searchText}
              />
            </form>


          </Search>


          {
            (!sm)
            &&
            <>
              <Typography
                variant="h6"
                component="div"
                sx={{ flexGrow: 1 }}>
              </Typography>





              {
                (role === 'USER_ROLE')
                &&
                <IconButton
                  size="large"
                  edge="end"
                  color="inherit"
                  sx={{ mr: 2 }}
                >
                  <ShoppingCartIcon />
                </IconButton>
              }





              <div>
                <IconButton
                  size="large"
                  aria-label="account of current user"
                  aria-controls="menu-appbar"
                  aria-haspopup="true"
                  onClick={handleMenu}
                  color="inherit"
                >
                  <AccountCircle />
                </IconButton>
                <Menu
                  id="menu-appbar"
                  anchorEl={anchorEl}
                  anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  sx={{
                    marginTop: (md) ? '4%' : '2.5%',
                    marginLeft: (md) ? '-4%' : '-2.5%',
                  }}
                  keepMounted
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  open={Boolean(anchorEl)}
                  onClose={handleClose}
                >
                  <MenuItem onClick={handleClose}>Profile</MenuItem>
                  <MenuItem onClick={handleLogout}>Logout</MenuItem>
                </Menu>
              </div>
              <Typography
                edge="end"
                variant="p"
                component="div"
                sx={{ textAlign: 'end' }}>
                {name}
              </Typography>
            </>
          }


        </Toolbar>
      </AppBar>
      {
        (categories !== undefined)
        &&
        <AppBar
          sx={{
            position: 'static',
            display: 'flex',
            justifyContent: 'center',
            backgroundColor: 'rgba(41, 128, 185, 1)',
            height: '7vh',
            '& #categoriesContainer': {
              position: 'relative',
              display: 'flex',
              padding: '1% 2.5vh',
              margin: 'auto',
              marginLeft: '2.5vw',
              marginRight: '2.5vw',
              cursor: 'pointer',
              ':hover': {
                backgroundColor: '#1F618D',
                transition: 'all 0.5s ease'
              }
            }
          }}>
          <Toolbar sx={{ display: 'flex', margin: 'auto', }}>
            {
              categories.map(
                e =>
                  <div
                    key={e.id}
                    id='categoriesContainer'
                    onClick={() => {
                      navigate(`?q=${e.name}`);
                      dispatch( searchProducts(e.name) );
                    } }
                  >
                    <Typography sx={{
                      display: 'flex',
                      justifySelf: 'center',
                      color: '#fff',
                      boderRight: '1px solid #000',
                      margin: 'auto',
                    }}>
                      {e.name}
                    </Typography>
                  </div>
              )
            }
          </Toolbar>
        </AppBar>
      }
    </Box >
  );
}

