import React from 'react';

import { Typography } from '@mui/material';
import { Box } from '@mui/system';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import InstagramIcon from '@mui/icons-material/Instagram';
import FacebookIcon from '@mui/icons-material/Facebook';

export const Footer = () => {
    return (
        <Box sx={{
            position: 'relative',
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'center',
            backgroundColor: '#000',
            color: '#fff',
            width: '100%',
            height: '30vh',
            zIndex: 999,
            '& #containerSocialMedias': {
                width: '100%',
                marginTop: '2.5vh',
                display: 'flex',
                justifyContent: 'center',

            },
            '& #developerContact': {
                width: '100%',
                marginTop: '2vh',
                display: 'flex',
                justifyContent: 'center',

            },
            '& .MuiSvgIcon-root': {
                fontSize: '40px',
                margin: 'auto',
            },

        }}>
            <div>

                <Typography
                    variant='h3'
                    fontSize='30px'
                    sx={{
                        marginTop: '5vh',

                    }}
                >
                    CONTACTO
                </Typography>

                <div id='containerSocialMedias'>
                    <WhatsAppIcon />
                    <InstagramIcon />
                    <FacebookIcon />
                </div>

            </div>
            <div id='developerContact'>
                <Typography
                    variant='p'
                    fontSize='20px'
                    sx={{

                    }}
                >
                    Developer contact: +542216106943 - ojedalucasgabriel2@gmail.com
                </Typography>
            </div>
        </Box>
    );
};