import React from 'react';
import { useSelector } from 'react-redux';

import ListItemText from '@mui/material/ListItemText';
import Container from '@mui/material/Container';
import { Box } from '@mui/material';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

import { styles__cardsMain } from '../../../styles/cardsMain';


export const CardProducts = () => {

  const { productRows } = useSelector(state => state.product);
  const { binProducts } = useSelector(state => state.bin);
  
  const { users } = useSelector(state => state.users);
  const { binUsers } = useSelector(state => state.bin);
  
  const { categories } = useSelector(state => state.categories);

  const theme = useTheme();
  const sm = useMediaQuery(theme.breakpoints.down('sm'));
  const md = useMediaQuery(theme.breakpoints.down('md'));
  const xl = useMediaQuery(theme.breakpoints.down('xl'));
  const lg = useMediaQuery(theme.breakpoints.down('lg'));

  return (
    <>
      <Container>

        <Box sx={styles__cardsMain( sm, md, lg, xl )}>

          <Container id='container'>

            <ListItemText primary='Productos activos' secondary={(productRows !== undefined) && productRows.length} />

            <ListItemText primary='Productos inactivos' secondary={(binProducts !== undefined) && binProducts.length} />

            {/* {
              productRows.map(e => (
                <MenuItem key={e._id} name={e.name} value={e.name}>{e.name}</MenuItem>
              ))
            } */}



          </Container>

          <Container id='container'>

            <ListItemText primary='Usuarios activos' secondary={(users !== undefined) && users.length} />

            <ListItemText primary='Usuarios inactivos' secondary={(binUsers !== undefined) && binUsers.length} />

            <ListItemText id='MiembrosDelEquipo' primary='Miembros del equipo:'/>

            {
            (users !== undefined)
            &&
              users.map(e => (
                (e.role === 'ADMIN_ROLE' || e.role === 'MODERATOR_ROLE')
                &&
                <ListItemText key={e._id} primary={e.name} secondary={e.role} />
              ))
            }

          </Container>

          <Container id='container'>

            <ListItemText primary='Categorias totales' secondary={(categories !== undefined) && categories.length} />

          </Container>


        </Box>
      </Container>


    </>
  );
};