import * as React from 'react';
import { useSelector } from 'react-redux';

import { Image } from 'cloudinary-react';

import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import IconButton from '@mui/material/IconButton';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import Typography from '@mui/material/Typography';

import { prepareProducts } from '../../../helpers/prepareProducts';

export const CardsMainHome = () => {

    const { productRows } = useSelector(state => state.product);
    const { filteredProducts } = useSelector(state => state.product);

    if (!filteredProducts) {
        if (productRows !== undefined) {
            var cleanProducts = prepareProducts(productRows);

            console.log(cleanProducts[0]);

        }
    } else {
        var cleanProducts = prepareProducts(filteredProducts);


    }

    return (
        <>
            {
                (productRows || filteredProducts)
                    ?
                    cleanProducts[0].map(e => (
                        <Card
                            key={e.id}
                            sx={{
                                width: '30ch',
                                height: '40ch',
                                cursor: 'pointer',
                                marginBottom: '2.5%',
                                marginLeft: '2.5%',
                                objectFit: 'contain',
                                overflow: 'cover',
                                '& #imageContainer': {
                                    display: 'flex',
                                    width: '30ch',
                                    height: '30ch',
                                    img: {
                                        display: 'flex',
                                        justifySelf: 'center',
                                        margin: 'auto',
                                    }
                                },
                                '& #productImage': {
                                    maxWidth: '30ch',
                                    maxHeight: '30ch',
                                    marginLeft: 0,
                                    marginRight: '5vh',
                                },
                                '& #title': {
                                    marginTop: '2.5%',
                                    visibility: 'hidden'
                                },
                                '& #price': {
                                    marginTop: '7.5%',
                                },
                                '& #ShoppingCartIcon': {
                                    position: 'absolute',
                                    color: '#0B5345',
                                    marginLeft: '15%',
                                    visibility: 'hidden',
                                },
                                ':hover': {
                                    '& #title': {
                                        visibility: 'visible',
                                    },
                                    '& #ShoppingCartIcon': {
                                        visibility: 'visible',
                                    },
                                    height: '43.25ch',
                                    transition: 'all 0.5s ease-out;',
                                }

                            }}>

                            <div id='imageContainer'>
                                <IconButton
                                    id='ShoppingCartIcon'
                                >
                                    <ShoppingCartIcon />
                                </IconButton>

                                {
                                    (e.img[0].imageUrl !== undefined)
                                        ?
                                        <Image
                                            id='productImage'
                                            cloudName="the-kings-company"
                                            publicId={e.img[0].imageUrl}
                                            alt='Product'
                                        />
                                        :
                                        <Image
                                            id='productImage'
                                            cloudName="the-kings-company"
                                            publicId={e.img[0]}
                                            alt='Product' />
                                }
                            </div>

                            <CardContent>
                                <Typography id='price' sx={{ fontSize: '18px' }} gutterBottom variant="h5" component="div">
                                    {`$${new Intl.NumberFormat('es-IN').format(e.price)}`}
                                </Typography>

                                <Typography id='title' variant="body2" color="text.secondary">
                                    {e.name}
                                </Typography>
                            </CardContent>
                        </Card>
                    ))
                    :
                    <Typography id='title' variant="body2" color="text.secondary">
                        No se ha encontrado ningn producto
                    </Typography>
            }
        </>

    );
}
